import { FaryguiInput, LoadPackage } from "../Lib/FaryguiInput";
import { SbCom } from "./SbCom";

const { ccclass, property } = cc._decorator;

@ccclass
export default class SbLoader extends cc.Component {
    onLoad() {
        LoadPackage("Farygui/Sb", (error: any) => {
            if (error) return;
            console.log("Farygui LoadPackage success");
            this.initUI();
        });
    }

    private initUI() {
        let rootCom = new FaryguiInput(this.node);
        let com = fgui.UIPackage.createObject("Sb", "Main") as fgui.GComponent;
        rootCom.addChild(com);
        const comCtrl = new SbCom(com);
    }
}