const { ccclass, property } = cc._decorator;

@ccclass
export default class Runable extends cc.Component {
    public static Attach(node: cc.Node, call: any) { 
        if (node["$runable"]) {
            node["$runable"].lateUpdate = call;
            return;
        }
        const r = node["$runable"] = node.addComponent(Runable);
        r.lateUpdate = call;
    }
    lateUpdate(): void {
    }
}