export class MapPaths {
    private map: { [type: string]: fgui.GObject };
    constructor(target: fgui.GComponent) {
        this.map = {};
        this.doMap(target["_children"], "");
    }
    private doMap(childs: fgui.GObject[], path: string) {
        if (!childs || childs.length <= 0) return;
        for (let idx = 0; idx < childs.length; idx++) {
            const c = childs[idx] as fgui.GComponent;
            const p = path + c.name;
            this.map[p] = c;
            if (c["_children"]) this.doMap(c["_children"], p + "/");
        }
    }
    public getChild<T extends fgui.GObject>(path: string): T {
        const r = this.map[path];
        console.assert(r != null && r != undefined, `error get path ${path}`);
        return r as T;
    }
}

export default class FUtil {

    public static Event: EventEX = new EventEX();

    public static createMapPaths(target: fgui.GComponent): MapPaths {
        return new MapPaths(target);
    }

    public static setOnceTapStart(com: fgui.GComponent, listener: Function, target?: any) {
        com.node.once(cc.Node.EventType.TOUCH_START, listener, target);
    }

    public static setTapStart(com: fgui.GComponent, listener: Function, target?: any) {
        com.node.on(cc.Node.EventType.TOUCH_START, listener, target);
    }

    public static setOnceTapEnd(com: fgui.GComponent, listener: Function, target?: any) {
        com.node.once(cc.Node.EventType.TOUCH_END, listener, target);
    }

    public static setClick(bt: fgui.GButton, listener, target = null) {
        bt.clearClick();
        bt.onClick(listener, target);
    }

    public static tweenStop(target: any) {
        fgui.GTween.kill(target, null, null);
    }

    public static stopScroll(list: fgui.GList) {
        const { scrollPane } = list;
        if (!scrollPane) return;
        scrollPane.percY = scrollPane.percY;
        // @ts-ignore
        scrollPane.refresh();
    }

    public static forceScrollBottom(list: fgui.GList) {
        list.scrollPane.scrollBottom(false);
        // @ts-ignore
        list.scrollPane.refresh();
    }

    public static scrollBottomAni(list: fgui.GList) {
        // @ts-ignore
        list.scrollPane.refresh();
        list.scrollPane.scrollBottom(true);
    }

    public static refresh(list: fgui.GList) {
        // @ts-ignore
        list.scrollPane.refresh();
    }

    public static isBottomMost(list: fgui.GList) {
        if (list.scrollPane.isBottomMost) return true;
        if (list.scrollPane.percY >= 0.96) return true;
        return false;
    }

    public static fadeIn(target: fgui.GComponent, delay = 0.01): fgui.GTweener {
        fgui.GTween.kill(target, null, null);
        return fgui.GTween.to(target.alpha, 1, 0.25).setTarget(target, "alpha").setDelay(0.01);
    }

    public static fadeOut(target: fgui.GComponent): fgui.GTweener {
        fgui.GTween.kill(target, null, null);
        return fgui.GTween.to(target.alpha, 0, 0.25).setTarget(target, "alpha");
    }

    public static moveX(target: fgui.GComponent, x: number): fgui.GTweener {
        return FUtil.moveXY(target, x, target.y);
    }

    public static moveY(target: fgui.GComponent, y: number): fgui.GTweener {
        return FUtil.moveXY(target, target.x, y);
    }

    public static moveXY(target: fgui.GComponent, x: number, y: number, duration: number = 0.2, ease: number = fgui.EaseType.QuartOut): fgui.GTweener {
        fgui.GTween.kill(target, null, null);
        return fgui.GTween.to2(target.x, target.y, x, y, duration).setEase(ease).setTarget(target, target.setPosition);
    }

    public static toHeight(target: fgui.GComponent, h: number, duration: number = 0.2) {
        return FUtil.toSize(target, target.width, h, duration);
    }

    public static toHeightByD(target: fgui.GComponent, h: number, fSpeed: number = 1) {
        let duration = 0.01;
        if (h != target.height) {
            let screenSize = cc.view.getVisibleSize();
            let d = Math.abs(h - target.height);
            if (d > screenSize.height) {
                if (h > screenSize.height) {
                    h = screenSize.height;
                } else if (target.height > screenSize.height) {
                    target.height = screenSize.height;
                }
                duration = Math.abs(h - target.height) * 0.001 * fSpeed;
            } else {
                duration = d * 0.001 * fSpeed;
            }
        }
        return FUtil.toSize(target, target.width, h, cc.misc.clampf(duration, 0.01, 0.5), h > target.height ? fgui.EaseType.QuartIn : fgui.EaseType.QuartOut);
    }

    public static kill(target: fgui.GComponent) {
        fgui.GTween.kill(target, null, null);
    }

    public static toSize(target: fgui.GComponent, w: number, h: number, duration: number = 0.25, ease: number = fgui.EaseType.QuartOut): fgui.GTweener {
        fgui.GTween.kill(target, null, null);
        return fgui.GTween.to2(target.width, target.height, w, h, duration).setEase(ease).setTarget(target, target.setSize);
    }

    public static toScaleY(target: fgui.GComponent, y: number, duration: number = 0.25, ease: number = fgui.EaseType.QuartOut): fgui.GTweener {
        fgui.GTween.kill(target, null, null);
        return fgui.GTween.to(target.scaleY, y, duration).setEase(ease).setTarget(target, target.scaleY);
    }

    public static centerH(target: fgui.GObject, offset: number = 0) {
        target.y = (target.parent.height - target.height) * 0.5 + offset;
    }

    public static getSpriteFrame(image: fgui.GImage): cc.SpriteFrame {
        //@ts-ignore
        return image._content.spriteFrame;
    }

    public static setSpriteFrame(image: fgui.GImage, sf: cc.SpriteFrame) {
        //@ts-ignore
        image._content.spriteFrame = sf;
    }

    public static openKeyboard(input: fgui.GTextInput) {
        // @ts-ignore
        if (!input._editBox) return;
        // @ts-ignore
        input._editBox.openKeyboard()
    }

    public static hideKeyboard(input: fgui.GTextInput) {
        // @ts-ignore
        if (!input._editBox) return;
        // @ts-ignore
        input._editBox.blur();
    }

    public static isTweening(target: any, propType?: any) {
        return fgui.GTween.isTweening(target, propType);
    }

    public static setCacheMode(target: fgui.GTextField, cacheMode: cc.Label.CacheMode) {
        // @ts-ignore
        if (!target._label) return;
        // @ts-ignore
        target._label.cacheMode = cacheMode;
    }

    public static setContentWidth(target: fgui.GTextField, contentWidth: number) {
        target.width = contentWidth;
        target.autoSize = fgui.AutoSizeType.Height;
        // @ts-ignore
        target.maxWidth = contentWidth;
    }

    public static updateRenderData(input: fgui.GRichTextField): number {
        // @ts-ignore
        if (!input._richText) return input.width;
        // @ts-ignore
        input._richText._layoutDirty = true;
        // @ts-ignore
        input._richText._updateRichText();
        // @ts-ignore
        return input._richText._labelWidth;
    }

    public static getChilds(parent: fgui.GComponent): Array<fgui.GObject> {
        return parent["_children"];
    }

    public static hideNode(com: fgui.GComponent, hash: string) {
        const parent = com.node;
        let nn = parent[hash];
        if (nn) nn.active = false;
    }

    public static createNode(com: fgui.GComponent, prefabEmoj: cc.Prefab, hash: string): Node {
        const parent = com.node;
        let nn = parent[hash];
        if (nn) {
            nn.active = true;
            return nn;
        }
        nn = cc.instantiate(prefabEmoj);
        parent.addChild(nn);
        parent[hash] = nn;
        return nn;
    }
}

export function isNullOrEmpty(text: string): boolean {
    if (
        text === undefined ||
        text === null ||
        text === "" ||
        text.length === 0
    ) {
        return true;
    }
    return false;
}