import { ListRendererEx } from "../Lib/FaryguiListEx";
import FUtil from "../Lib/FaryguiUtil";
import { GListEx, GListFast } from "../Lib/GListEx";

const log = console.log;

export class SbCom {
    constructor(com: fgui.GComponent) {
        const mapObjs = FUtil.createMapPaths(com);
        const listView = mapObjs.getChild("ListView") as fgui.GList;
        const tgAuto = mapObjs.getChild("tgAuto") as fgui.GButton;
        const data: ItemData[] = [];
        const items: ItemCtrl[] = [];
        for (let iF = 0; iF < 100; iF++) {
            const matches: Match[] = [];
            const lMatch = iF == 0 ? 2 : Math.round(50 + Math.random() * 50);
            // const lMatch = 0;
            for (let iM = 0; iM < lMatch; iM++) {
                // const type = (iF + iM) % itemInfo.length;
                const type = 0;
                const info = itemInfo[type];
                matches.push({
                    name: `F${iF} M${iM}`,
                    type: type,
                    url: info[0] as string,
                    height: info[1] as number,
                    strData: randomTextSpecial(Math.floor(10 + Math.random() * 10)),
                });
            }
            data.push({
                idx: iF,
                isExpand: true,
                matches: matches,
                ref: null,
            });
        }
        ListRendererEx(listView, (index: number, item: fgui.GComponent) => {
        // listView.itemRenderer = (index: number, item: fgui.GComponent) => {
            let itemCtrl: ItemCtrl = item["$itemCtrl"]
            if (!itemCtrl) {
                itemCtrl = new ItemCtrl(listView, item, data[index]);
                item["$itemCtrl"] = itemCtrl;
                items.push(itemCtrl);
            }
            itemCtrl.refresh(data[index]);
            log("Obj render match");
        });
        this.data = data;
        listView.numItems = data.length;
        
        com._partner.schedule(() => {
            if (tgAuto.selected) {
                this.randomData();
            }
        }, 0.2, cc.macro.REPEAT_FOREVER);
    }

    data: ItemData[] = null;
    private randomData() {
        const data = this.data;
        for (let idx = 0; idx < data.length; idx++) {
            const l = data[idx];
            if (!l.isExpand) continue;
            l.matches.forEach(m => {
                m.strData = randomTextSpecial(Math.floor(10 + Math.random() * 10));
                if (m.doRefreshUi) m.doRefreshUi();
                if (m.doRanShowUi) m.doRanShowUi();
            });
        }
    }
}

function randomTextSpecial(length: number): string {
    const chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*";
    let result = "";
    for (let i = 0; i < length; i++) {
        result += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return result;
}

const itemInfo = [
    ["ui://rb6r5pqoesa1b", 300],
    ["ui://rb6r5pqoesa118", 400],
    ["ui://rb6r5pqoesa11b", 600],
];
function getDateStr(time: number) {
    const sd = getDate(time);
    return `${sd.year}-${sd.month}-${sd.day} ${sd.hour}:${sd.minute}:${sd.second}.${sd.millisecond}`;
}
function getDate(time: number) {
    let d = time > 0 ? new Date(time) : new Date();
    let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate(),
        month = d.getMonth() < 9 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1),
        year = d.getFullYear(),
        hour = d.getHours() < 10 ? '0' + d.getHours() : d.getHours(),
        minute = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes(),
        second = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds();
    const m = d.getMilliseconds();
    let millisecond = m < 10 ? '0' + m : m.toString();
    if (m < 100) {
        millisecond = '0' + m;
    }
    return {
        time: (+d),
        year: year,
        month: month,
        day: day,
        hour: hour,
        minute: minute,
        second: second,
        millisecond: millisecond
    };
}

interface Match { 
    name: string,
    type: number, 
    url: string, 
    height: number,
    strData?: string,
    doRefreshUi?: () => void,
    doRanShowUi?: () => void,
}

interface ItemData {
    idx: number,
    isExpand: boolean,
    matches: Match[],
    ref: ItemCtrl,
}

class ItemCtrl {
    list: fgui.GList;
    item: fgui.GComponent;
    lb?: fgui.GLabel;
    lb2?: fgui.GLabel;
    lb3?: fgui.GLabel;
    content: fgui.GComponent;
    list_content: fgui.GList;
    btDropClose: fgui.GComponent;
    btDropOpen: fgui.GComponent;
    //
    data?: ItemData;
    hBase: number;
    isMoving = false;

    constructor(list: fgui.GList, item: fgui.GComponent, data: ItemData) {
        this.data = data;
        this.list = list;
        this.item = item;
        this.lb = item.getChild("lb") as fgui.GLabel;
        this.lb2 = item.getChild("lb2") as fgui.GLabel;
        this.lb3 = item.getChild("lb3") as fgui.GLabel;
        this.content = item.getChild("content") as fgui.GComponent;
        this.list_content = this.content.getChild("list_content") as fgui.GList;
        const btDrop = item.getChild("btDrop") as fgui.GButton;
        this.btDropClose = btDrop.getChild("close") as fgui.GComponent;
        this.btDropOpen = btDrop.getChild("open") as fgui.GComponent;
        this.hBase = item.height;
        this.content.height = 0;
        this.content.visible = false;
        btDrop.onClick(this.onclick, this);
        // this.list_content.setVirtual();
        this.initList();
    }
    isInitList = false;
    initList() {
        const self = this;
        if (self.isInitList) return;
        self.isInitList = true;
        const itemRenderer = (index: number, item: fgui.GComponent) => {
            const idxMatch = index;
            const math = self.data.matches[idxMatch];
            if (item["@data"] === math) { 
                return;
            } else if (item["@data"]) {
                item["@data"].doRefreshUi = undefined;
                item["@data"].doRanShowUi = undefined;
            }
            log("Obj render cell", math.name);
            item["@data"] = math;
            const btEx = item.getChild("btEx") as fgui.GButton;
            // log("init com match", data.idx, idxMatch);
            if (btEx) {
                FUtil.setClick(btEx, () => {
                    const idxMatch = self.data.matches.indexOf(math);
                    self.onBtExClick(idxMatch);
                });
            }
            const btAdd = item.getChild("btAdd") as fgui.GButton;
            if (btAdd) {
                FUtil.setClick(btAdd, () => {
                    const idxMatch = self.data.matches.indexOf(math);
                    self.onBtAddClick(idxMatch);
                });
            }
            const btRemove = item.getChild("btRemove") as fgui.GButton;
            if (btRemove) {
                FUtil.setClick(btRemove, () => {
                    const idxMatch = self.data.matches.indexOf(math);
                    self.onBtRemoveClick(idxMatch);
                });
            }
            const lb = item.getChild("lb") as fgui.GLabel;
            const lbSub = item.getChild("lbSub") as fgui.GLabel;
            const lbSub2 = item.getChild("lbSub2") as fgui.GLabel;
            const lbSub3 = item.getChild("lbSub3") as fgui.GLabel;
            const lbSub4 = item.getChild("lbSub4") as fgui.GLabel;
            math.doRefreshUi = () => {
                if (lb) {
                    lb.text = math.name;
                }
                if (lbSub) {
                    lbSub.text = "Type cell is " + math.type;
                }
                if (lbSub2) {
                    lbSub2.text = "Height cell is " + math.height;
                }
                if (math.strData && lbSub3) {
                    lbSub3.text = math.strData;
                }
                if (lbSub4) {
                    lbSub4.text = getDateStr(Date.now());
                }
            };
            math.doRanShowUi = () => {
                for (let idx = 0; idx < item._children.length; idx++) {
                    const c = item._children[idx];
                    if (c.name[0] == 'n') {
                        c.visible = Math.random() > 0.8;
                    }
                }
            }
            math.doRefreshUi();
        }
        this.list_content.itemRenderer = itemRenderer;
        return;
        // ListRendererEx4(this.list_content,
        // (index: number) => {
        //     return self.data.matches[index];
        // },itemRenderer);
    }

    onBtExClick(idxMatch: number) { 
        log("onBtExClick", idxMatch);
        const match = this.data.matches[idxMatch];
        const type = (++match.type) % itemInfo.length;
        const info: any = itemInfo[type];
        match.type = type;
        match.url = info[0];
        match.height = info[1];
        this.refresh(this.data);
    }

    onBtAddClick(idxMatch: number) { 
        log("onBtAddClick", idxMatch);
        const matches = this.data.matches;
        const m = matches[idxMatch];
        const c = matches.splice(idxMatch, matches.length - idxMatch);
        matches.push({
            name: m.name,
            type: m.type,
            url: m.url,
            height: m.height,
            strData: m.strData,
        });
        if (c.length > 0) { 
            matches.push(...c);
        }
        GListEx.addItemByUrl(this.list_content, idxMatch);
        this.refresh(this.data);
    }

    onBtRemoveClick(idxMatch: number) { 
        log("onBtRemoveClick", idxMatch);
        this.data.matches.splice(idxMatch, 1);
        GListEx.removeItemEx(this.list_content, idxMatch);
        // this.list_content.removeChildToPoolAt(idxMatch);
        this.refresh(this.data);
    }

    countRefresh = 0;
    offsetContentBot = 30;
    refresh(data: ItemData) {
        this.countRefresh++;
        if (this.lb2) this.lb2.text = "Refresh: " + this.countRefresh;
        if (this.lb3) this.lb3.text = "Count cells: " + data.matches.length;
        data.ref = this;
        this.data = data;
        if (this.lb) this.lb.text = "Idx " + data.idx;
        this.content.visible = true;
        this.btDropClose.visible = !data.isExpand;
        this.btDropOpen.visible = data.isExpand;
        FUtil.kill(this.content);
        if (!data.isExpand) {
            FUtil.toHeightByD(this.item, this.hBase, 1.2)
            .onUpdate(this.doSpan, this)
            .onComplete(() => {
                this.item.height = this.hBase;
                this.doSpan();
                this.content.height = 0;
                this.content.visible = false;
            }, this);
            return;
        }
        if(!this.item.node.activeInHierarchy){
            log("skip list");
            return;
        }
        const self = this;
        GListFast.fastUpdateHeight(this.list_content, data.matches.length, (index: number) => {
            return self.data.matches[index];
        });
        // this.list_content.numItems = data.matches.length;
        // this.list_content.updateBounds();
        const contentHeight = this.list_content.scrollPane.contentHeight;
        this.list_content.scrollPane.viewHeight = contentHeight;
        // this.list_content.updateBounds();
        const hContent = contentHeight;
        const hTo = this.hBase + this.list_content.y + hContent + this.offsetContentBot;
        if (this.countRefresh <= 1) {
            this.item.height = hTo;
            this.doSpan();
            return;
        }
        FUtil.toHeightByD(this.item, hTo, 1.0)
        .onUpdate(this.doSpan, this)
        .onComplete(() => {
            this.item.height = hTo;
            // this.list.touchEffect = true;
            this.doSpan();
        }, this);
    }

    doSpan() { 
        let h = this.item.height - this.hBase - this.offsetContentBot;
        if (h <= 0) h = 0;
        this.content.height = h;
    }

    onclick() {
        if (!this.data) return;
        this.data.isExpand = !this.data.isExpand;
        this.refresh(this.data);
    }
}