import Runable from "./Runable";

const log = console.log;
const warn = console.warn;
interface ItemInfo { 
    y?: number;
    width: number;
    height: number;
    obj?: fgui.GComponent | any;
    updateFlag?: number;
    selected?: boolean;
}

export class GListEx {
    public static addItemByUrl(list: fgui.GList, index: number, url = undefined) {
        if (!list._virtualItems) {
            const obj = list.getFromPool(url);   
            list.addChildAt(obj, index);
            return;
        }
        const c = list._virtualItems.splice(index, list._virtualItems.length - index);    
        list._virtualItems.push({
            y: 0,
            width: list.viewWidth,
            height: 0,
            updateFlag: 0
        });
        if (c.length > 0) { 
            list._virtualItems.push(...c);
        }
    }

    public static removeItemEx(list: fgui.GList, index: number) {
        const itemRemove = list._virtualItems[index];
        list._virtualItems.splice(index, 1);
        GListEx.doHide(list, itemRemove);
    }

    public static countObjCreate = 0;

    public static doHide(list: fgui.GList, item: ItemInfo) {
        if (!item.obj) return;
        const obj = item.obj;
        item.obj = undefined;
        obj.countRefresh = 0;
        fgui.GTween.kill(obj, null, null);
        obj.visible = false;
        list.removeChildToPool(obj);
        GListEx.countObjCreate--;
        log("Obj remove", GListEx.countObjCreate);
    }

    public static doShow(list: fgui.GList, item: ItemInfo, url: string) {
        if (item.obj) {
            warn("Obj not remove before");
            GListEx.doHide(list, item);
        }
        const obj = list.getFromPool(url);
        obj.y = item.y;
        list.addChild(obj);
        item.obj = obj;
        GListEx.countObjCreate++;
        if (list.autoResizeItem && obj.width != list.viewWidth) {
            obj.setSize(list.viewWidth, obj.height, true);
        }
        log("Obj create", GListEx.countObjCreate);
        return obj;
    }
}
export class GListFast { 
    public static moveChileTo(child: fgui.GComponent, start: number, end: number, duration: number) { 
        if (!child['setNodeY']) {
            child['setNodeY'] = (y: number) => {
                var xv = child._x;
                var yv = -y;
                if (!child._pivotAsAnchor) {
                    xv += child.node.anchorX * child._width;
                    yv -= (1 - child.node.anchorY) * child._height;
                }
                if (child._pixelSnapping) {
                    xv = Math.round(xv);
                    yv = Math.round(yv);
                }
                child._node.setPosition(xv, yv);
            }
        }
        fgui.GTween.to(start, end, duration).setEase(end > start ? fgui.EaseType.QuartIn : fgui.EaseType.QuartOut).setTarget(child, child.setNodeY);
    }
    public static fastUpdateHeight(list: fgui.GList, numItem: number, getInfo: (index: number) => { url: string, height: number }) {
        const isAni = true;
        let idx;
        let child: fgui.GComponent | any;
        let curY = 0;
        let cw = 0, ch = 0;
        const viewWidth = list.viewWidth;
        const viewHeight = list.viewHeight;
        const lastVirtualItems = list._virtualItems;
        const virtualItems: ItemInfo[] = [];
        list._virtualItems = virtualItems;
        if (lastVirtualItems) { 
            for (let idx = 0; idx < lastVirtualItems.length; idx++) {
                const item = lastVirtualItems[idx];
                if(idx >= numItem) {
                    GListEx.doHide(list, item);
                    continue;
                }
                child = item.obj;
                const { url, height } = getInfo(idx);
                if (child && child.resourceURL != url) {
                    GListEx.doHide(list, item);
                }
            }
        }
        for (idx = 0; idx < numItem; idx++) {
            const { url, height } = getInfo(idx);
            child = lastVirtualItems && idx < lastVirtualItems.length ? lastVirtualItems[idx].obj : undefined;
            if (!child) {
                if (curY != 0) {
                    curY += list._lineGap;
                }
                virtualItems.push({
                    y: curY,
                    width: viewWidth,
                    height: height,
                    updateFlag: 0
                });
                curY += height;
                continue;
            }
            if (curY != 0) curY += list._lineGap;
            if (!isAni || !child.countRefresh) {
                child.y = curY;
            } else if (child.y != curY) {
                fgui.GTween.kill(child, null, null);
                const yFrom = child.countRefresh ? child.y : curY - list._lineGap;
                let d = Math.abs(curY - yFrom) * 0.001;
                if (d < 0.1) d = 0.1;
                else if (d > 0.5) d = 0.5;
                // fgui.GTween.to(yFrom, curY, d).setEase(curY > yFrom ? fgui.EaseType.QuartIn : fgui.EaseType.QuartOut).setTarget(child, child.setNodeY);
                GListFast.moveChileTo(child, yFrom, curY, d);
                child._y = curY;
            }
            if (!child.countRefresh) {
                child.countRefresh = 1;
            } else {
                child.countRefresh++;
            }
            if (list._autoResizeItem && child.width != viewWidth) {
                child.setSize(viewWidth, child.height, true);
            }
            const curYLast = curY;
            if (child) {
                curY += Math.ceil(child.height)
            } else {
                curY += height;
            }
            virtualItems.push({
                y: curYLast,
                width: viewWidth,
                height: curY - curYLast,
                updateFlag: 0,
                obj: child
            });
        }
        ch = curY;
        cw = Math.ceil(viewWidth);
        list.handleAlign(cw, ch);
        list.setBounds(0, 0, cw, ch);
        list._boundsChanged = false;
        // refresh cell
        const gList = list;
        gList['getInfo'] = getInfo;
        gList.updateBounds = () => {
            GListFast.updateBoundsFast(gList, getInfo, true);
        };
        if (!gList["$isCheck"]) {
            gList["$isCheck"] = true;
            const screenH = cc.view.getVisibleSize().height;
            let topL = -gList.localToGlobal(0, screenH).y;
            Runable.Attach(gList.node, () => {
                let top = -gList.localToGlobal(0, screenH).y;
                if (top != topL) {
                    topL = top;
                    GListFast.updateBoundsFast(gList, gList['getInfo'], false);
                }
            });
        }
        if (numItem <= 0) return;
        gList._partner.callLater(gList.updateBounds);
    }

    public static updateBoundsFast(gList: fgui.GList, getInfo: (index: number) => { url: string, height: number }, isScroll = false) {
        const isAni = true;
        const viewWidth = gList.viewWidth;
        const viewHeight = gList.viewHeight;
        const virtualItems = gList._virtualItems;
        const screenH = cc.view.getVisibleSize().height;
        let top = -gList.localToGlobal(0, screenH).y;
        const bottom = top + screenH;
        let isSkip = false;
        let countRender = 0;
        let curY = 0;
        // const start = performance.now();
        for (let idx = 0; idx < virtualItems.length; idx++) {
            if (curY != 0) {
                curY += gList._lineGap;
            }
            const item = virtualItems[idx];
            if (isSkip || item.y - item.height > bottom) {
                // hide bottom
                isSkip = true;
                GListEx.doHide(gList, item);
                curY += item.height;
                continue;
            }
            if (item.y + item.height < top) {
                // hide top
                GListEx.doHide(gList, item);
                curY += item.height;
                continue;
            }
            // do render
            const { url, height } = getInfo(idx);
            // if (item.obj && item.obj.resourceURL != url) {
            //     doHide(item);
            // }
            let obj = item.obj;
            if (obj) {
                obj.visible = true;
            } else {
                if (isScroll) {
                    obj = GListEx.doShow(gList, item, gList._defaultItem);
                    if (item.height != obj.height) {
                        obj.setSize(viewWidth, item.height, true);
                    }
                    obj._partner.unscheduleAllCallbacks();
                    obj._partner.callLater(() => {
                        GListEx.doHide(gList, item);
                        const nObj = GListEx.doShow(gList, item, url);
                        gList.itemRenderer(idx, nObj);
                        if (item.height != Math.ceil(nObj.height)) {
                            // TODO:
                            // gList._partner.callLater(gList.updateBoundsFast.bind(gList));
                        }
                    }, 0.01);
                } else {
                    obj = GListEx.doShow(gList, item, url);
                }
            }
            const child = item.obj;
            if (child) { 
                if (!child.countRefresh) {
                    child.countRefresh = 1;
                } else {
                    child.countRefresh++;
                }
                if (!isScroll && child && child.resourceURL === url) {
                    gList.itemRenderer(idx, child);
                    // TODO
                    // if (item.height != Math.ceil(child.height)) {
                    //     item.height = Math.ceil(child.height);
                    // }
                }
                countRender++;
                if (child.y == curY) {
                    // skip
                } else if (!isAni || !child.countRefresh || !isScroll) {
                    child.y = curY;
                    item.y = curY;
                } else {
                    fgui.GTween.kill(child, null, null);
                    const yFrom = child.countRefresh ? child.y : curY - gList._lineGap;
                    let d = Math.abs(curY - yFrom) * 0.001;
                    if (d < 0.1) d = 0.1;
                    else if (d > 0.5) d = 0.5;
                    // fgui.GTween.to(yFrom, curY, d).setEase(curY > yFrom ? fgui.EaseType.QuartIn : fgui.EaseType.QuartOut).setTarget(child, child.setNodeY);
                    GListFast.moveChileTo(child, yFrom, curY, d);
                    child._y = curY;
                    item.y = curY;
                }
            }
            curY += item.height;
        }
        gList._boundsChanged = false;
    }
}