import Runable from './Runable';

const log = console.log;
export function ListRendererEx(list: fgui.GList, itemRenderer: (index: number, item: fgui.GComponent) => void) {
    const items: fgui.GComponent[] = [];
    list.itemRenderer = (index: number, item: fgui.GComponent) => {
        itemRenderer(index, item);
        if (!item["$items"]) {
            item["$items"] = items;
            items.push(item);
        }
    };
    if (list._virtual) return;
    let countRender = 0;
    const scrollPane = list.scrollPane;
    Runable.Attach(list.node, () => {
        let countR = 0;
        let top = scrollPane.posY;
        if (top < 0) top = 0;
        const bottom = top + list.height;
        let isSkip = false;
        for (let idx = 0; idx < items.length; idx++) {
            const item = items[idx];
            if (isSkip || item.y + item.height < top) {
                item._node.active = false;
            } else if (item.y - item.height > bottom) {
                item._node.active = false;
                isSkip = true;
            } else {
                item._node.active = true;
                countR++;
            }
        }
        if (countRender != countR) {
            countRender = countR;
            log("List count render", countR);
        }
    });
}